/* Global styles */

html {
  background-color: #d3d7ff;
}

@font-face {
  src: url('./fonts/TravelingTypewriter.ttf');
  font-family: Traveling Typewriter;
}

.App {
  text-align: center;
  font-family: Traveling Typewriter;
}

.Container {
  margin: auto;
  margin-top: 5%;
  width: 90%;
  font-size: calc(3px + 2vmin);
}

.Container-flex {
  display: flex;
  flex-wrap: wrap;
}

.Container-left {
  text-align: left;
  font-family: Arial;
  padding: 2%;
  width: 45%;
  float: left;
  border: solid black 1px;
}

.Container-right {
  text-align: left;
  font-family: Arial;
  padding: 2%;
  width: 45%;
  float: right;
  border: solid black 1px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Line-separator {
  margin-top: 3em;
  width: 100%;
  background-color: black;
  opacity: 30%;
  height: .1em;
}

.Navbar {
  margin-top: 3%;
  margin: auto;
  width: 100%;
  height: 6em;
  position: relative;
  background-color: #000000;
}

.Navbar-logo {
  vertical-align: middle;
  height: 6em;
  cursor: pointer;
}

.Navbar-navigation {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.Navbar-item {
  color: #2196F3;
  text-decoration: none;
  display: inline-block;
  padding: 1rem;
}

.Navbar-item:hover,
.Navbar-item:active {
  color: white;
  cursor: pointer;
}

.Navbar-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.Navbar-spacer {
  flex: 1;
}

.Toggle-Button {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}

.Toggle-Button:focus {
  outline: none;
}

.Toggle-Button-line {
  width: 40px;
  height: 3px;
  background: gainsboro;
}

.Button-aanestys {
  margin-top: 5%;
  width: 300px;
  height: 70px;
  border: none;
  background-color: #265AA9;
  color: #d1ebff;
  font-family: Traveling Typewriter;
  font-size: 2rem;
}

.Button-aanestys:hover {
  background-color: #2196F3;
}

.Button-aanestys-disabled {
  margin-top: 5%;
  width: 300px;
  height: 100px;
  border: none;
  background-color: grey;
  color: #d1ebff;
  font-family: Traveling Typewriter;
  font-size: 2rem;
}

.Backdrop {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 100;
}

.Side-Drawer {
  height: 100vh;
  background-color: #181A1B;
  box-shadow: 2px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 400px;
  z-index: 200;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
}

.Side-Drawer.open {
  transform: translateX(0);
}

.Side-Drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Side-Drawer-item {
  text-align: left;
  color: #2196F3;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: calc(15px + 1vmin);
  padding: 1rem;
}

.Side-Drawer-item:hover, 
.Side-Drawer-item:active {
  color: white;
}

.Side-Drawer li {
  margin: 0.5rem 0;
}

@media (max-width: 900px) {
  .Navbar-items {
    display: none;
  }
}

@media (min-width: 901px) {
  .Toggle-Button {
    display: none;
  }
}

.Line-separator-full {
  height: 2px;
  background-color: black;
  width: 100%;
}

.Loading-animation {
  margin-top: 10%;
  width: 10vw;
}

.Heading {
  font-size: calc(10px + 2vmin);
  margin-top: 5%;
}

.Topic-item {
  margin-top: 3%;
}

.Topic-item-winner {
  font-size: 3rem;
  margin-top: 3%;
}

.Chosen-img {
  text-align: center;
  margin-top: 5%;
  max-width: 55px;
}

.Loading-animation {
  max-width: 30%;
}

.Error, .Success {
  margin: 10px 0px;
  padding:12px;
}

.Error {
  background-color: #FFD2D2;
  color: #D8000C;
}

.Success {
  color: #2196F3;
  background-color: #d1ebff;
}

.Aihe-input {
  background-color: #181A1B;
  height: 50px;
  width: 50%;
  font-size: larger;
  color: white;
}

.Add-button {
  font-family: Traveling Typewriter;
  width: 100px;
  height: 60px;
  border: solid #2196F3 2px;
  margin-top: 4%;
}

.Add-button:hover {
  background-color: #2196F3;
  border: solid black 1px;
}

/* AdminView */

.AdminView-aanestajia-flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.AdminView-voterCount {
  margin-left: .5em;
  margin-right: .5em;
}

.AdminView-arrow-icon {
  width: 2em;
  cursor: pointer;
}

/* TopicSelection */

.TopicSelection-buttons {
  display: flex;
  justify-content: space-around;
}

.TopicSelection-Choice-button {
  text-align: center;
  font-family: Traveling Typewriter;
  width: 300px;
  max-width: 50%;
  height: 80px;
  margin-top: 2em;
  margin-bottom: 5em;
  border: none;
  font-weight: bold;
  background-color: #265AA9;
  color: #d3d7ff;
  font-size: 2rem;
}

@media(max-width: 597px) {
  .TopicSelection-Choice-button {
    width: 180px;
    font-size: .8rem;
  }
}

.TopicSelection-footer-text {
  margin-bottom: 4em;
}

.TopicSelection-Choice-button:hover {
  background-color: #2196F3;
}

.Victor {
  border: solid 5px green;
}

.Crown-image {
  width: 6em;
  margin-left: 5em;
}

.LastVoteLandingPage-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Winner-container {
  background-image: url(./resources/confetti.gif);
  width: 100vw;
  height: 85vh;
}

.Paivan-tk-flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Paivan-tk {
  margin-top: 5em;
  width: 20em;
  border-left: solid 2px #2196F3;
  border-right: solid 2px #2196F3;
}

.Uusi-button {
  font-family: Traveling Typewriter;
  width: 100px;
  height: 60px;
  border: solid #2196F3 2px;
  margin-top: 3em;
  transform: translateY(-2em);
}

.Uusi-button:hover {
  background-color: #2196F3;
  border: solid black 1px;
}

@media(max-width: 447px) {
  .Heading {
    margin-top: 4em;
  }
  .Topic-item-winner {
    font-size: 10rem;
  }
  .Crown-image {
    margin-top: 4em;
    width: 15em;
  }
  .Container {
    margin-top: 3em;
    font-size: large;
  }
  .Aihe-input {
    width: 15em;
  }
  .Topic-item {
    font-size: small;
  }
  .Victor {
    border: solid 2px green;
  }
}

.Checked-icon {
  width: 3em;
}

.Vote-closed {
  margin-top: 3em;
}

.All-topics {
  margin-top: 4em;
  margin-bottom: 4em;
  text-align: left;
}

.Valiotsikko-flex {
  display: flex;
  justify-content: center;
}

.Horizontal-separator {
  margin-left: 1.5em;
  border-right: solid 2px #2196F3;
  margin-right: 1.5em;
}

.Admin-input {
  width: 30em;
  height: 3em;
}
